.add-product-page {
    padding-bottom: 100px;
    flex: 1;

    display: flex;
    justify-content: center;

    &-form {
        padding: 32px 230px;

        max-width: 1130px;

        flex: 1;

        display: grid;
        grid-template-columns: 1fr;
        grid-row-gap: 25px;

        box-sizing: border-box;

        &-title {
            text-align: center;
        }

        &-photos {
            flex: 1;
            display: flex;
            &-container {
                padding: 16px;

                width: 100%;
                height: 100%;

                flex: 1;

                display: grid;
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-row-gap: 30px;

                color: #000;

                font-size: 16px;

                border-radius: 5px;
                border: 1px solid #dedee0;
                box-sizing: border-box;

                &::placeholder {
                    color: #66666666;
                }

                &[error="true"] {
                    border-color: red;
                }
            }
        }
    }
}
