.profile-tab {
    padding: 15px 0;

    width: 160px;
    height: 80px;

    fill: none;
    color: black;

    &[active] {
        fill: #349A89;
        color: white;
    }

    &:hover {
        fill: #238978;
    }
        
    cursor: pointer;

    &-container {
        padding: 15px 0;
        width: 100%;
        height: 100%;

        & div {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        
            background-color: transparent;
        }
    
        &-main-text {
            font-size: 22px;
        }
    
        &-minor-text {
            font-size: 13px;
        }
    }
}